import _ from "./underscore";

let cloneOptions = (opt) => {
    return _.clone(opt);
};

let cloneData = (data) => {
    return JSON.parse(JSON.stringify(data));
};

let getDefaultProps = ( dProps, target={} ) => {
    for( var p in dProps ){
        if( !!p.indexOf( "_" ) ){
            if( !dProps[ p ] || !dProps[ p ].propertys ){
                //如果这个属性没有子属性了，那么就说明这个已经是叶子节点了
                if( _.isObject( dProps[ p ] ) && !_.isFunction(dProps[ p ]) && !_.isArray(dProps[ p ]) ){
                    target[ p ] = dProps[ p ].default;
                } else {
                    target[ p ] = dProps[ p ];
                };
            } else {
                target[ p ] = {};
                getDefaultProps( dProps[ p ].propertys, target[ p ] );
            }
        };
    }
    return target;
};


export { cloneOptions, cloneData, getDefaultProps }